import React, { Component } from 'react';
import { Row, Col, Menu, Icon, Form, Dropdown } from 'antd';
import './style.scss';

@Form.create({ name: 'formMenuTabTop' })
class MenuTabTop extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.menu = (
      <Menu>
        <Menu.Item>
          <a
            target="_parent"
            rel="noopener noreferrer"
            href="#/students/StatisOfRegisNumbers/classHC"
          >
            TK số SV đăng ký theo lớp hành chính
          </a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="http://www.taobao.com/">
            TK số SV đăng ký theo học phần
          </a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="http://www.tmall.com/">
            TK số SV chưa đăng ký
          </a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="http://www.tmall.com/">
            TK số SV đã đăng ký
          </a>
        </Menu.Item>
      </Menu>
    );
  }

  handleClick = e => {
    // console.log('click ', e);
    this.setState({
      current: e.key,
    });
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };

  render() {
    return (
      <Form onSubmit={this.handleSearch} className="menuTabTopMargin">
        <Row gutter={24}>
          <Col span={24}>
            <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
              <Menu.Item key="statistic">
                <Dropdown overlay={this.menu}>
                  <a href="#/">
                    <Icon type="setting" /> Thống kê <Icon type="down" />
                  </a>
                </Dropdown>
              </Menu.Item>
              <Menu.Item key="cancel">
                <Icon type="setting" />
                Hủy
              </Menu.Item>
              <Menu.Item key="restore">
                <Icon type="setting" />
                Khôi phục
              </Menu.Item>
              <Menu.Item key="changeClass">
                <Icon type="setting" />
                Chuyển lớp
              </Menu.Item>
              <Menu.Item key="printReport">
                <Icon type="setting" />
                In báo cáo
              </Menu.Item>
              <Menu.Item key="printExcel">
                <Icon type="setting" />
                Xuất Excel
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default MenuTabTop;



